import React from 'react';
import ReactDom from 'react-dom';
import PropTypes from 'prop-types';
import {autobind} from 'core-decorators';
import Dialog from 'components/Dialog';
import './style.scss';

@autobind
class Alert extends React.Component{
    static propTypes={
        parent:PropTypes.object,
        state:PropTypes.string,
        show:PropTypes.bool,
        title:PropTypes.string,
        content:PropTypes.any,
        confirm:PropTypes.func,
        button:PropTypes.string,
        maskClose:PropTypes.bool,
    }

    static defaultProps={
        parent:null,//this
        state:null,//state
        show:false,//state
        title:'',//
        content:'',//
        confirm:()=>{},//
        button:'',//
        maskClose:true,//
    }

    /*
        <Alert
            parent={this}
            state="show1"
            show={show1}
            title=""
            content=""
            confirm={()=>{console.log('')}}
            button=""
        />
    */

    /*
        $alert.show({
            title:'',
            content:'',
            confirm:()=>{console.log('')},
            button:'',
        });
    */

    constructor(props){
        super(props);
        this.state={
            isMethod:false,
            option:{
                title:'',//
                content:'',//
                confirm:()=>{},//
                button:'',//
                maskClose:true,//
            },
            parent:this,
            state:'show',
            show:false,
        };
    }

    clickHandle(){
        const {isMethod,option}=this.state;
        const {parent,state}=!isMethod?this.props:this.state;
        const {confirm}=!isMethod?this.props:option;
        const This=!isMethod?parent:this;

        This.setState({
            [state]:false,
        });

        confirm&&confirm();
    }

    show(option){
        this.setState({
            isMethod:true,
            show:true,
        },()=>{
            option=Object.assign(this.state.option,option);

            this.setState({
                option,
            });
        });
    }

    render(){
        const {isMethod,option}=this.state;
        const {parent,state,show}=!isMethod?this.props:this.state;
        const {title,content,button,maskClose}=!isMethod?this.props:option;

        return(
            <div className="Alert">
                {
                    show?
                    <Dialog
                        parent={parent}
                        state={state}
                        showDialog={show}
                        title={title||''}
                        content={content}
                        maskClose={maskClose}
                    >
                        <div className="main">
                            {content}
                        </div>

                        <div className="end1">
                            <span onClick={(ev)=>{this.clickHandle()}}>{button}</span>
                        </div>
                    </Dialog>
                    :
                    ''
                }
            </div>
        )
    }
}

let dom=document.getElementById('AlertWrap');

if(!dom){
    dom=document.createElement('div');
    dom.id='AlertWrap';
    document.body.appendChild(dom);
}

let $alert=null;

ReactDom.render(<Alert ref={(dom)=>{
    $alert=dom;
}} />,dom);

export{
    Alert,
    $alert,
};


