import 'babel-polyfill';
import React from 'react';
import ReactDOM from 'react-dom';
import {Provider} from 'react-redux';
import registerServiceWorker from './registerServiceWorker';
import {createHashHistory} from 'history';
import {ConnectedRouter,routerMiddleware} from 'react-router-redux';
import {Route} from 'react-router-dom';
import store from 'store';
import App from 'pages/App';
import {htmlFontSize,consoleNull} from 'js/yydjs';
import 'css/index.css';

/*

    createBrowserHistoryH5history Api
    createMemoryHistoryReact Nativehistory
    createHashHistoryhash history Api
*/
export const browser=createHashHistory();
export const middleware=routerMiddleware(browser);

htmlFontSize();//
if(window.location.hostname!=='localhost')consoleNull(['log']);//

ReactDOM.render(
    <Provider store={store}>
        <ConnectedRouter history={browser}>
            <Route path='/' component={App} />
        </ConnectedRouter>
    </Provider>,
    document.getElementById('root')
);
registerServiceWorker();



