//typeof7
function Type(obj){
    var arr=['null','nan','function','number','string','array','object'];
    if(obj===null){
        return 'null';
    }
    if(obj!==obj){
        return 'nan';
    }
    if(typeof Array.isArray==='function'){
        if(Array.isArray(obj)){ //isArray()
            return 'array';
        }
    }else{                      //toString
        if(Object.prototype.toString.call(obj)==='[object Array]'){
            return 'array';
        }
    }
    return (typeof obj).toLowerCase();
};

