//oninput="inputType(this,0)"
function inputType(This,index){
    var arr=[
                This.value.replace(/[^\d]+/g,''),//0
                This.value.replace(/[^a-zA-Z]+/g,''),//1
                This.value.replace(/[^a-zA-Z]+/g,'').toLowerCase(),//2
                This.value.replace(/[^a-zA-Z]+/g,'').toUpperCase(),//3
                This.value.replace(/[^\w]+/g,''),//4
                This.value.replace(/[^\w]+/g,'').toLowerCase(),//5
                This.value.replace(/[^\w]+/g,'').toUpperCase(),//6
                This.value.replace(/[^\u2E80-\u9FFFa-zA-Z]+/g,''),//7
            ];
    This.value=arr[index||0];
};

