//cookie
var cookie={
    set:function(key,value,sec){
        var value=value;
        var sec=sec||60*60*24*30;
        var type=Type(value);
        var oDate=new Date();

        switch(type){
            case 'object':
            case 'array':
                    value=JSON.stringify(value);
                break;
        }

        oDate.setSeconds(oDate.getSeconds()+sec);
        oDate=oDate.toGMTString();
        document.cookie=key+'='+encodeURIComponent(value)+';expires='+oDate;
    },
    get:function(key){
        var str=document.cookie;
        var reg=new RegExp('(^|(;\\s))'+key+'=([^;\\s]+)((;\\s)|$)');
        var result=str.match(reg);

        result=result?decodeURIComponent(result[3]):'';

        try{
            result=JSON.parse(result);
        }catch(e){}

        return result;
    },
    getKeys:function(){
        var str=document.cookie;
        var reg1=/\=+/g;
        var reg2=/(\;|[\;\s])+/g;

        try{
            if(str.length){
                str=str.replace(reg1,'":"');
                str=str.replace(reg2,'","');
                str='{"'+str;
                str+='"}';
                str=JSON.parse(str);
            }else{
                str={};
            }
        }catch(e){}

        return str;
    },
    getAll:function(){
        var json={};
        var keys=this.getKeys();

        for(var attr in keys){
            json[attr]=this.get(attr);
        }

        return  json;
    },
    remove:function(key){
        var oDate=new Date();

        oDate.setDate(oDate.getDate()-1);
        oDate=oDate.toGMTString();
        document.cookie=key+'=;expires='+oDate;
    },
    clear:function(){
        var keys=this.getKeys();

        for(var attr in keys){
            this.remove(attr);
        }
    },
};

//Store
var Store=function(){
    this.name='Store';
};

Store.prototype={
    init:function(options){
        this.store=function(){
            return options.type;
        };
        return this;
    },
    set:function(key,value){
        var type=Type(value);

        switch(type){
            case 'object':
            case 'array':
                    this.store().setItem(key,JSON.stringify(value));
                break;
            default :
                    this.store().setItem(key,value);
        }

    },
    get:function(key){
        var value=this.store().getItem(key);

        try{
            value=JSON.parse(value);
        }catch(e){}

        return value;
    },
    getAll:function(){
        var json={};
        var value='';

        for(var attr in this.store()){
            try{
                value=JSON.parse(this.store()[attr]);
            }catch(e){}
            json[attr]=value;
        }
        return  json;
    },
    remove:function(key){
        this.store().removeItem(key);
    },
    clear:function(){
        this.store().clear();
    },
};

