//js
//linkHref
//className()
//json(json)
function jsStyle(linkHref,className,json){
    var sheets=document.styleSheets;//
    var sheet=null;

    for(var i=0;i<sheets.length;i++){
        if(sheets[i].href){
            var sHref=sheets[i].href;
            sHref=sHref.substring(sHref.lastIndexOf('/')+1,sHref.length);

            if(sHref==linkHref){
                sheet=sheets[i];//
            }
        }
    }

    var rules=sheet.cssRules||sheet.rules;//
    var rule=null;

    for(var i=0;i<rules.length;i++){
        if(rules[i].selectorText==className){
            rule=rules[i];//
            for(var attr in json){
                rule.style[attr]=json[attr];
            }
        }
    }
    return rule.cssText;
};

//?json(submitstr='1'&str1='2')
function strToJson(str){
    var str=str||window.location.search;
    var reg=/&+/g;
    var reg1=/=+/g;

    try{
        if(str.match(/.+=/)){
            str=decodeURI(str);
            str=str.replace('?','');
            str=str.replace(reg,'","');
            str=str.replace(reg1,'":"');
            str='{"'+str+'"}';
            str=JSON.parse(str);
        }else{
            str={};
        }
    }catch(e){
        str={};
    }
    return str;
};

//?json(str={}&str1={})
//json
function strToJson1(str){
    var str=str||window.location.search;
    var reg=/&+/g;
    var reg1=/=+/g;
    var reg2=/^\?.+$/;

    try{
        if(str.match(/.+=/)){
            str=decodeURI(str);
            str=reg2.test(str)?str.replace('?','"'):'"'+str;
            str=str.replace(reg,',"');
            str=str.replace(reg1,'":');
            str='{'+str+'}';
            str=JSON.parse(str);
        }else{
            str={};
        }
    }catch(e){
        str={};
    }
    return str;
};

//json?url
//json()
//arr(jsonkey)
//href(href)
function jsonToStr(json,arr,href){
    var str='';
    var href=href||(window.location.origin+window.location.pathname);

    for(var i=0;i<arr.length;i++){
        delete json[arr[i]];
    }
    for(var attr in json){
        str+=attr+'='+json[attr]+'&';
    }
    str=href+'?'+str.substr(0,str.length-1);
    return str;
};

