function keyCode(){
    document.onkeyup=function(ev){
        var ev=ev||window.event;
        var oP=document.createElement('p');
        var aString=String.fromCharCode(ev.keyCode);
        var json={27:'Esc',112:'F1',113:'F2',114:'F3',115:'F4',116:'F5',117:'F6',118:'F7',119:'F8',120:'F9',121:'F10',122:'F11',123:'F12',44:'PrtScr',145:'Scroll',19:'Pause',192:'`',189:'-',187:'=',8:'',45:'Insert',36:'Home',33:'PgUp',144:' NumLock',111:' /',106:' *',109:' -',9:'Tab',219:'[',221:']',13:'Enter',46:'Delete',35:'End',34:'PgDn',103:' 7',104:' 8',105:' 9',107:' +',20:'Capslock',186:'',222:'',220:'',100:' 4',101:' 5',102:' 6',16:'Shift',188:'',190:'',191:'/',38:'',97:' 1',98:' 2',99:' 3',17:'Ctrl',91:'Window',92:'Window',18:'Alt',32:'',93:'',37:'',40:'',39:'',96:' 0',110:' .'};

        if(json[ev.keyCode]){
            aString=json[ev.keyCode];
        }
        oP.innerHTML=''+':'+aString+' '+''+':'+ev.keyCode;
        document.body.appendChild(oP);
    };
};



