//(arrange)
function A(m,n){
    return f(m)/f(m-n);
};
//(combination)
function C(m,n){
    return f(m)/(f(m-n)*f(n));
};
//
function f(num){
    if(num<=1){
        return 1;
    }
    return num*f(num-1);
};

//
function changeXY(size){
    var arr=[];
    var len=size*size;
    var row = 0;
    var col = 0;
    var min = 0;
    var max = size - 1;

    for(var i=0;i<len;i++){
        arr.push(row*size+col);
        if(row==min&&col<max){
            col=col+1;
        }
        else if(col==max&&row<max){
            row=row+1;
        }
        else if(row==max&&col>min){
            col=col-1;
        }
        else if(col==min&&row>min){
            row=row-1;
        }
        if(row-1==min&&col==min){
            min=min+1;
            max=max-1;
        }
    }
    return arr;
};

//
function changeXY1(arr,size){
    var newArr=[];
    var iNow=0;

    (function(){
        if(iNow==size){
            return;
        }
        for(var i=0;i<arr.length;i++){
            if(i%size==iNow){
                newArr.push(arr[i]);
            }
        }
        iNow++;
        arguments.callee();//
    })();
    return newArr;
}



