function cBub(ev){
    var ev=ev||window.event;
    if(ev.stopPropagation)ev.stopPropagation(); //
    ev.cancelBubble=true;//ie
};

//
function pDef(ev){
    var ev=ev||window.event;
    if(ev.preventDefault)ev.preventDefault();   //
    ev.returnValue=false;//ie
};

//fix
function fix(ev){
    pDef(ev);
};

