//
//reg>type>if
//arr[{if:'','reg':/^$/,type:'number','value':,'str':''}]
//if
//reg
//type
//value
//hint
//endFn
//errorFn
//msec3
function alertss(arr,endFn,errorFn,msec){
    var onOff=true;
    var errorIndex=-1;
    var regJson={//type
        number:{
            name:'',
            reg:/^[0-9]+$/,
        },
        aa:{
            name:'',
            reg:/^[a-z]+$/,
        },
        AA:{
            nmae:'',
            reg:/^[A-Z]+$/,
        },
        aA:{
            name:'',
            reg:/^[a-zA-Z]+$/,
        },
        aa1:{
            name:'',
            reg:/^[a-z0-9]+$/,
        },
        AA1:{
            name:'',
            reg:/^[A-Z0-9]+$/,
        },
        aA1:{
            name:'',
            reg:/^\w+$/,
        },
        zh:{
            name:'',
            reg:/^[\u2E80-\u9FFF]+$/,
        },
        zhEn:{
            name:'',
            reg:/^[\u2E80-\u9FFFa-zA-Z]+$/,
        },
        mobile:{
            name:'',
            reg:/^1[3-9]{1}\d{9}$/,
        },
        identity:{
            name:'',
            reg:/^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/,
        },
        bankCard:{
            name:'',
            reg:/^[0-9]{8,28}$/,
        },
        user:{
            name:'',
            reg:/^[\w-]{3,16}$/,
        },
        password:{
            name:'',
            reg:/^[a-zA-Z0-9]{6,20}$/,
        },
        email:{
            name:'',
            reg:/^([\w\.-]+)@([\da-z\.-]+)\.([a-z\.]{2,6})$/,
        },
    };

    for(var i=0;i<arr.length;i++){
        if(arr[i].reg){
            if(!arr[i].reg.test(arr[i].value)){
                alerts(arr[i].hint,msec);
                onOff=false;
                errorIndex=i;
                break;
            }
        }else if(arr[i].type){
            if(regJson[arr[i].type]&&!regJson[arr[i].type].reg.test(arr[i].value)){
                alerts(arr[i].hint||''+regJson[arr[i].type].name,msec);
                onOff=false;
                errorIndex=i;
                break;
            }
        }else if(arr[i].if){
            alerts(arr[i].hint,msec);
            onOff=false;
            errorIndex=i;
            break;
        }
    }

    errorFn&&errorFn(errorIndex);
    onOff&&endFn&&endFn();
};

//jsonkey
function jsonHasKey(json,key){
    if(Type(json)!='object'){
        return false;
    }
    return key in json;
};

//json
function allHaveValue(obj){
    var bool=true;

    if(Type(obj)=='array'){
        for(var i=0;i<obj.length;i++){
            if(!obj[i]&&obj[i]!==0){
                bool=false;
                break;
            }
        }
    }else if(Type(obj)=='object'){
        for(var attr in obj){
            if(!obj[attr]&&obj[attr]!==0){
                bool=false;
                break;
            }
        }
    }else{
        if(!obj&&obj!==0){
            bool=false;
        }
    }
    return bool;
};

