function colorPrice(value){
    var symbol=+value>0?'+':'';

    return symbol+getDecimal(value);
};

//
function limitLength(value,length){
    var value=value;
    var arr=value+'';

    arr=arr.split('.');
    if(arr.length==length){
        arr[1]=arr[1].substring(0,length);
        value=arr.join('.');
    }
    return value;
};

