import {ajaxWrap,alerts,Type,sStore,lStore,customEvent,strToJson} from 'js/yydjs';
import store from 'store';
import md5 from 'md5';
import {browser} from 'src';
import * as actions from 'store/actions';

let URL='/app/http';

//ajax
const ajaxWrapFn=function(json){
    let oldPathname=browser.location.pathname;
    let noLoginArr=[
                    {
                        path:'/login',//
                    },
                    {
                        path:'/account',//
                        exact:true,
                    },
                    {
                        path:'/account/personal_center',//
                        exact:true,
                    },
                    {
                        path:'/exponent',//
                        exact:true,
                    },
                    {
                        path:'/information',//
                    },
                    {
                        path:'/popularize',//
                        exact:true,
                    },
                    {
                        path:'/data',//
                        exact:true,
                    },
                    {
                        path:'/article_info',//
                    },
                    {
                        path:'/activity_center',//
                    },
                    {
                        path:'/advance',//
                    },
                ];
    let sign='';
    let token=lStore.get('token');

    for(let attr in json.data){
        sign+=json.data[attr];
    }
    sign=md5(sign+'0123456789');

    ajaxWrap({
        code:'0000',
        url:json.url,
        type:json.type,
        data:json.data,
        closeToForm:json.closeToForm,
        dataType:json.dataType,
        headers:json.headers||{sign,token},
        getXhr:json.getXhr,
        progress:json.progress,
        before:function(xhr){
            //loading
            store.dispatch(actions.loadingControl(true));
        },
        after:function(xhr){
            //loading
            store.dispatch(actions.loadingControl(false));
        },
        success:function(res){
            let pathname=browser.location.pathname;

            if(oldPathname!=pathname)return;//setState

            //
            json.finally&&json.finally(res);

            //codecode
            if(Type(res)=="object"){
                if(res.code=='0000'){
                    json.success&&json.success(res);
                    return;
                }else if(res.code=='CRM00001'||res.code=='CRM00002'||res.code=='CRM00003'){
                    //app
                    if(strToJson().insertApp){
                        window.location.href='gopage://login';
                    }

                    let redirect=true;
                    for(let value of noLoginArr){
                        if(value.exact){
                            if(pathname==value.path){
                                redirect=false;
                                break;
                            }
                        }else{
                            if(pathname.match(value.path)){
                                redirect=false;
                                break;
                            }
                        }
                    }

                    if(redirect){
                        customEvent.emit('needLogin');
                    }
                    return;
                }else if(res.msg){
                    if(res.code!='U0029'){
                        alerts(res.msg);
                    }
                    return;
                }
                alerts("");
            }
        },
        error:function(error){
            alerts("");
            store.dispatch(actions.loadingControl(false));
        },
    });
};

const demoApi=function(json,endFn){
    ajaxWrapFn({
        url:URL+'/pay/listWallet',
        type:'post',
        data:json,
        dataType:'json',
        success:function(res){
            endFn&&endFn(res);
        },
    });
};


const uploadOSS=function(json,endFn){
    ajaxWrapFn({
        url:URL+'/uploadOSS',
        type:'post',
        data:json,
        closeToForm:true,
        dataType:'json',
        success:function(res){
            endFn&&endFn(res);
        },
    });
};

const uploadImage=function(ev,endFn,index){
    let file=ev.currentTarget.files[0];
    let formData=new FormData();

    if(file){
        formData.append('file',file);
        uploadOSS(formData,(res)=>{
            alerts('',true);
            endFn&&endFn(res,index);
        });
    }
}

