import React from 'react';
import ReactDom from 'react-dom';
import PropTypes from 'prop-types';
import {autobind} from 'core-decorators';
import Dialog from 'components/Dialog';
import './style.scss';

@autobind
class Confirm extends React.Component{
    static propTypes={
        parent:PropTypes.object,
        state:PropTypes.string,
        show:PropTypes.bool,
        title:PropTypes.string,
        content:PropTypes.any,
        confirm:PropTypes.func,
        cancel:PropTypes.func,
        lButton:PropTypes.string,
        rButton:PropTypes.string,
        maskClose:PropTypes.bool,
    }

    static defaultProps={
        parent:null,//this
        state:null,//state
        show:false,//state
        title:'',//
        content:'',//
        confirm:()=>{},//
        cancel:()=>{},//
        lButton:'',//
        rButton:'',//
        maskClose:true,//
    }

    /*
        <Confirm
            parent={this}
            state="show"
            show={show}
            title=""
            content={<h1></h1>}
            confirm={()=>{console.log('')}}
            cancel={()=>{console.log('')}}
            lButton=""
            rButton=""
        />
    */

    /*
        $confirm.show({
            title:'',
            content:<h1></h1>,
            confirm:()=>{console.log('')},
            cancel:()=>{console.log('')},
            lButton:'',
            rButton:'',
        });
    */

    constructor(props){
        super(props);
        this.state={
            isMethod:false,
            option:{
                title:'',//
                content:'',//
                confirm:()=>{},//
                cancel:()=>{},//
                lButton:'',//
                rButton:'',//
                maskClose:true,//
            },
            parent:this,
            state:'show',
            show:false,
        };
    }

    clickHandle(bool){
        const {isMethod,option}=this.state;
        const {parent,state}=!isMethod?this.props:this.state;
        const {confirm,cancel}=!isMethod?this.props:option;
        const This=!isMethod?parent:this;

        This.setState({
            [state]:false,
        });

        bool?confirm&&confirm():cancel&&cancel();
    }

    show(option){
        this.setState({
            isMethod:true,
            show:true,
        },()=>{
            option=Object.assign(this.state.option,option);

            this.setState({
                option,
            });
        });
    }

    render(){
        const {isMethod,option}=this.state;
        const {parent,state,show}=!isMethod?this.props:this.state;
        const {title,content,lButton,rButton,maskClose}=!isMethod?this.props:option;

        return(
            <div className="Confirm">
                {
                    show?
                    <Dialog
                        parent={parent}
                        state={state}
                        showDialog={show}
                        title={title||''}
                        content={content}
                        maskClose={maskClose}
                    >
                        <div className="main">
                            {content}
                        </div>

                        <div className="end1">
                            <span onClick={(ev)=>{this.clickHandle(false)}}>{lButton}</span>
                            <span onClick={(ev)=>{this.clickHandle(true)}}>{rButton}</span>
                        </div>
                    </Dialog>
                    :
                    ''
                }
            </div>
        )
    }
}

let dom=document.getElementById('ConfirmWrap');

if(!dom){
    dom=document.createElement('div');
    dom.id='ConfirmWrap';
    document.body.appendChild(dom);
}

let $confirm=null;

ReactDom.render(<Confirm ref={(dom)=>{
    $confirm=dom;
}} />,dom);

export{
    Confirm,
    $confirm,
};


